<template>
	<div class="overflow-auto" data-ss="aaa">
		<h4 class="text-left">Table.vue</h4>	
		<div id="btnGroup" data-ss="zz" class="text-right">
		<b-button-group>
			<template v-for="(btn, idx) in btnArr">
			<b-button
				v-if="btn.routeName !== 'ent'"
				:key="idx"
				:variant="btn.variant"
				@click="getTableItemsBy({table:{title:btn.caption},fetchOption:{routeName:btn.routeName}})"	
			>
			{{ btn.caption }}
			</b-button>
			
			<b-dropdown 
				v-else
				right
				:key="idx"
				text="Entity Info"
			>
				
				<template v-for="(ent, idx) in entArr">
					<b-dropdown-item 
						:key="idx"						@click="getTableItemsBy({table:{title:ent.caption,fieldLang:ent.fieldLang},fetchOption:{routeName:ent.routeName}})"		
					>
						{{ent.caption}}
					</b-dropdown-item>
				</template>
				
			</b-dropdown>
			</template>
		</b-button-group>
		</div>
		
		<div class="text-left">
		
		<b-table striped hover caption-top
			id="my-talbe"
			:items="table.items"
			:per-page="perPage"
			:current-page="currentPage"
			small
		>
			<template slot="table-caption">
				<h4 class="text-center">{{table.title}}</h4>
			</template>
		
		</b-table>
		
		
		<div class="offset-9 text-right" v-show="rows > perPage" >
		<!-- <p class=""> -->
			<span>Total items: {{ rows }},</span>&nbsp;
			<span>Current Page: {{ currentPage }}</span>
			<br>
			<!-- <span>每页行数:</span> -->
			<label>每页行数:</label>
			<b-form-select v-model="perPage" :options="options"></b-form-select>	
		<!-- </p> -->
		
		</div>
		<br>
		<b-pagination
			v-model="currentPage"
			v-show="rows > perPage"
			align="right"
			:total-rows="rows"
			:per-page="perPage"
			aria-controls="my-table"
		></b-pagination>
		
		</div>
		
		
	</div>
</template>

<script>
import Vue from 'vue';

//Importing as a Vue.js plugin
//import { ButtonGroupPlugin } from 'bootstrap-vue';
//Vue.use(ButtonGroupPlugin);

//import { ButtonPlugin } from 'bootstrap-vue';
//Vue.use(ButtonPlugin);

//Importing individual components
//register them globally

//import { BButtonGroup } from 'bootstrap-vue';
//Vue.component('b-button-group', BButtonGroup);

//import { BButton } from 'bootstrap-vue';
//Vue.component('b-button', BButton);

//import { BPagination } from 'bootstrap-vue';
//Vue.component('b-pagination', BPagination);

//import { BTable } from 'bootstrap-vue';
//Vue.component('b-table', BTable);

//import { BFormSelect } from 'bootstrap-vue'
//Vue.component('b-form-select', BFormSelect)

//var host=document.location.host;

//use plugin
import { ButtonGroupPlugin } from 'bootstrap-vue';
//Vue.use(ButtonGroupPlugin);
new Vue('b-button-group',{ButtonGroupPlugin});

import { ButtonPlugin } from 'bootstrap-vue';
//Vue.use(ButtonPlugin);
new Vue('b-button',{ButtonPlugin});


import { PaginationPlugin } from 'bootstrap-vue'
//Vue.use(PaginationPlugin)
new Vue('b-pagination',{PaginationPlugin});

import { TablePlugin } from 'bootstrap-vue'
Vue.use(TablePlugin)
//new Vue('b-table',{TablePlugin});

import { FormSelectPlugin } from 'bootstrap-vue'
Vue.use(FormSelectPlugin)
//new Vue('b-form-select',{FormSelectPlugin});

import { mapState, mapActions } from 'vuex'

const btnItems=[
	{variant:'success',caption:'Initial Info',routeName:''},
	{variant:'info',caption:'Server Info',routeName:'serv'},
	{variant:'warning',caption:'Config Info',routeName:'conf'},
	{variant:'primary',caption:'Envirement Info',routeName:'env'},
	{variant:'dark',caption:'Entity Info',routeName:'ent'},
];

const entItems=[
	{caption:'专利信息',routeName:'ent-pat',fieldLang:'chn'},
	{caption:'Projet Info',routeName:'ent-pro',fieldLang:'en'},
	{caption:'Thesis Info',routeName:'ent-the',fieldLang:'en'},
]
export default {
    name: 'TheTable',
    data:function() {
		return {
		//表格表头设置
		//fields:()={
		//	return
		//},
			perPage: 10,
			currentPage: 1,
			btnArr:btnItems,
			entArr:entItems,
			options: [
				{ value: 5, text: '5' },
				{ value: 10, text: '10' },
				{ value: 20, text: '20' },
				{ value: 50, text: '50', }
			]
		}
	},
	computed: mapState({
		table: state => state.table,
		fetchOption: state => state.fetchOption,
		
		rows: state => state.table.items.length,
	}),
	methods: {
		
		... mapActions({
			getTableItemsBy: 'asyChangeTable'
		})
	},
	//computed:{
		
		//...mapState({
			
		//})
	//},
	components:{
		//BButtonGroup,
		//BButton
	}
}

</script>

