//引入fontawesome核心库
import { library } from '@fortawesome/fontawesome-svg-core'

//引入solid(free)
import { 
	faUserSecret,
	faUmbrella,
	faSignOutAlt 
} from '@fortawesome/free-solid-svg-icons'

//引入brand(free)
import { 
	faFontAwesome, 
} from '@fortawesome/free-brands-svg-icons'

//引入fontawesome的vue库
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

//添加引入的图标到核心库
library.add(
	//solid(free)
	faUserSecret,
	faUmbrella,
	faSignOutAlt,
	
	//brand(free)
	faFontAwesome
)

var faIcon = FontAwesomeIcon

export {faIcon}