const entFieldName={
	pat:{
		patnum:{chn:'专利编号'},
		topic:{chn:'题目'},
		type:{chn:'类型'},
		patowner:{chn:'所有人'},
		inventor:{chn:'发明人'},
		author:{chn:'撰写人'},
	},
	the:{},
	pro:{}
};

const debug = process.env.NODE_ENV !== 'production'
const BankEndUrl=debug?'http://localhost:8090':'';
//const postmanUrl='https://a9baf5bf-a2cf-43a8-8db8-2fb9aa3f5acc.mock.pstmn.io';

var getUrlByRoute=(routeName)=>{
	let url=routeName?BankEndUrl+`/${routeName.split('-').join('/')}`:''
	return url
}

var getEntFieldName=(entType,lang='')=>{
	let ent=Object.keys(entFieldName).includes(entType)?entType:'';
	let langObj=ent?entFieldName[ent]:{};
	let resObj={};
		
	if(lang && Object.keys(langObj).length){
		for(var key in langObj){
			resObj[key]=langObj[key][lang];
		}
	}
	// console.log('getEntFieldName(): resObj');
	// console.table(resObj);
	return resObj;
};

async function asyFetchByRoute(opt={routeName:'',method: 'GET'}){
	let url=getUrlByRoute(opt.routeName)
	let typeArr=['blob','text','json','formData'];
	//js原生fetch()方法的第二参数
	let optDefault={
			method: '', // *GET, POST, PUT, DELETE, etc
			//mode: 'cors', // no-cors, cors, *same-origin
			//cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
			//credentials: 'same-origin', // include, *same-origin, omit
			//headers: {
				//'Content-Type': 'application/json',
				// 'Content-Type': 'application/x-www-form-urlencoded',
			//},
			//redirect: 'follow', // manual, *follow, error
			//referrer: 'no-referrer', // no-referrer, *client
			//body:JSON.stringify({zz:11}), // body data type must match "Content-Type" header while method=='POST' or 'PUT'
			type:'json',
		};
	let res={ok:false}
	let result={
			ok:false,
			cont:[
				{ 'No.':1, age: 40},
				{ 'No.':2, age: 21},
				{ 'No.':3, age: 89},
				{ 'No.':4, age: 38}
			]
		}
	
	opt=Object.assign({},optDefault,opt);
	
	if(url && typeArr.includes(opt.type)){
		res=await fetch(url,opt);
		//console.log(res);
	}
	
	if(res.ok){
		result.ok=true;
		switch(opt.type){
			case 'blob':
				result.cont=await res.blob();
				break;
			case 'text':
				result.cont=await res.text();
				break;
			case 'formData':
				result.cont=await res.formData();
				break;
			default:
				result.cont=await res.json();
				break;
		}
	}
	
	//async函数返回的是Promise对象
	return result;
	
}

async function asyGetTableItemsByRoute(fetchOption={},fieldLang=''){
	let result=await asyFetchByRoute(fetchOption)
	let cont=result.cont
	let ent=cont.hasOwnProperty('ent')?cont['ent']:''
	let list=ent&&cont.hasOwnProperty('list')?cont['list']:[]
	let itemObj=ent?getEntFieldName(ent,fieldLang):{}
	let arr=[]
	
	if(!result.ok){
		return result.cont
	}
	
	if(ent){
		//数组list的值为对象
		for(var i=0;i<list.length;i++){
			arr[i]={};
					
			if(fieldLang=='chn'){
				arr[i]['序号']=i+1;
			}else{
				arr[i]['No.']=i+1;
			}
			for(var k in list[i]){
				if(Object.keys(itemObj).includes(k)){
					arr[i][itemObj[k]]=list[i][k];
				}else{
					arr[i][k]=list[i][k];
				}
			}
		}
					
		if(!list.length){
			arr=[{type:ent,result:'no info!'}];
		}
	}
				
	if(!ent){
		let n=1;
		for(var key in cont){
			arr.push({'No.':n++,'key':key,'value':cont[key]});
		}
	}
	//async函数返回的是Promise对象
	return arr
}

export {
	asyFetchByRoute,
	asyGetTableItemsByRoute,
	
}