<template>
<div>
  <b-navbar toggleable="lg" type="dark" variant="info">
    <b-navbar-brand href="#">
		<font-awesome-icon :icon="['fab', 'font-awesome']" />
	</b-navbar-brand>

    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

    <b-collapse id="nav-collapse" is-nav>
      <b-navbar-nav>
        <b-nav-item href="#">Link</b-nav-item>
        <b-nav-item href="#" disabled>Disabled</b-nav-item>
		
		<template v-for="(nav, idx) in navArr">
			<b-nav-item 
				v-if="nav.routeName !== 'ent'"
				:key="idx"
				@click="changeTableItemsBy({table:{title:nav.caption},fetchOption:{routeName:nav.routeName}})" 
			>
				{{ nav.caption }}
			</b-nav-item>
		
			<b-nav-item-dropdown 
				v-else
				:key="idx"
				text="Entity"
			>
				<template v-for="(ent, idx) in entArr">
					<b-dropdown-item 
						:key="idx"
						@click="changeTableItemsBy({table:{title:ent.caption,fieldLang:ent.fieldLang},fetchOption:{routeName:ent.routeName}})"						
					>
						{{ ent.caption }}
					</b-dropdown-item>
				</template>
			
			</b-nav-item-dropdown>
		</template>		
      </b-navbar-nav>

      <!-- Right aligned nav items -->
      <b-navbar-nav class="ml-auto">
        <b-nav-form>
          <b-form-input size="sm" class="mr-sm-2" placeholder="Search"></b-form-input>
          <b-button size="sm" class="my-2 my-sm-0" type="submit">Search</b-button>
        </b-nav-form>

        <b-nav-item-dropdown text="Lang" right>
          <b-dropdown-item href="#">EN</b-dropdown-item>
          <b-dropdown-item href="#">ES</b-dropdown-item>
          <b-dropdown-item href="#">RU</b-dropdown-item>
          <b-dropdown-item href="#">FA</b-dropdown-item>
        </b-nav-item-dropdown>

        <b-nav-item-dropdown right>
          <!-- Using 'button-content' slot -->
          <template v-slot:button-content>
            <em>User</em>
          </template>
			<b-dropdown-item href="#">Profile</b-dropdown-item>
			<b-dropdown-item href="#">
				<font-awesome-icon :icon="['fas', 'sign-out-alt']" />
				Sign Out
			</b-dropdown-item>
        </b-nav-item-dropdown>
      </b-navbar-nav>
    </b-collapse>
  </b-navbar>
</div>
</template>

<script>

import Vue from 'vue'

import { mapActions } from 'vuex'

import { NavbarPlugin } from 'bootstrap-vue'
//Vue.use(NavbarPlugin)
new Vue('b-navbar',{NavbarPlugin})

//import TheTable from './TheTable.vue'

const navItems=[
	{caption:'Initial',routeName:'',fieldLang:''},
	{caption:'Server',routeName:'serv',fieldLang:''},
	{caption:'Config',routeName:'conf',fieldLang:''},
	{caption:'Envirement ',routeName:'env',fieldLang:''},
	{caption:'Entity ',routeName:'ent',fieldLang:''}
]

const entItems=[
	{caption:'专利信息',routeName:'ent-pat',fieldLang:'chn'},
	{caption:'Projet Info',routeName:'ent-pro',fieldLang:''},
	{caption:'Thesis Info',routeName:'ent-the',fieldLang:''}
]


export default {
	name: 'TheNavbar',
	data(){
		return {
			navArr:navItems,
			entArr:entItems
		}
	},
	methods: {
		... mapActions({
			changeTableItemsBy: 'asyChangeTable'
		})
	},
	
}
</script>


<style >


</style>
