// root actions

//import state from './state.js'

import {asyGetTableItemsByRoute} from '../util.js'

export default {
	
	async asyChangeTable({dispatch},opt){
		await dispatch('asyGetTableItems',opt)
	},
	async asyGetTableItems({commit,state},opt){
			
		opt.table = Object.assign({},state.table,opt.table)
		opt.fetchOption = Object.assign({},state.fetchOption,opt.fetchOption)
			
		opt.table.items=await asyGetTableItemsByRoute(opt.fetchOption,opt.table.fieldLang)
			
		
		//context.commit('changeTableItems',arr)
		commit('changeTable',opt.table)
		commit('changeFetchOption',opt.fetchOption)
			
	}
	
}